.app-downlode{
    margin: auto auto;
    margin: 100px;
    font-size: max(3vw, 20px);
    text-align: center;
    font-weight: 500;
}

.app-downlode-platforms{
    display: flex;
    justify-content: center;
    gap: max(2vw, 10px);
    margin-top: 40px;
}

.app-downlode-platforms img{
    width: max(30vw, 120px);
    max-width: 180px;
    transition: 0.5s;
    cursor: pointer;
}

.app-downlode-platforms img:hover{
    transform: scale(1.05);
}